import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  InputGroup,
  InputGroupAddon,
  Label,
  Row,
} from 'reactstrap';
import { connect } from 'react-redux';
import IntlMessages from '../../../helpers/IntlMessages';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { adminRoot } from '../../../constants/defaultValues';
import StateButton from '../../../components/StateButton';

const handleSuccessButtonClick = () => {
  return new Promise((success) => {
    setTimeout(() => {
      success('Everything went right!');
    }, 1400);
  });
};

const Links = ({ match }) => {
  const [disabled, setDisabled] = useState(true);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.links" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <Card className="mb-4">
            <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="primary"
                onClick={() => {
                  setDisabled(!disabled);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div>
            <CardBody>
              <CardTitle>
                <IntlMessages id="user.update-links" />
              </CardTitle>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="forms.linkedin" />
                </Label>
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-linkedin btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="linkedin"
                    value="https://linkedin.com"
                    disabled={disabled}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="forms.facebook" />
                </Label>
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-facebook btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="facebook"
                    value="https://facebook.com"
                    disabled={disabled}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="forms.github" />
                </Label>
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-github btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="github"
                    value="https://github.com"
                    disabled={disabled}
                  />
                </InputGroup>
              </FormGroup>
            </CardBody>
          </Card>
        </Colxx>
      </Row>
      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              disabled={!disabled}
              href={`${adminRoot}/profile/experience`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
            {disabled && (
              <Button
                color="primary"
                className="m-3"
                href={`${adminRoot}/profile/contact`}
              >
                <IntlMessages id="forms.next" />
              </Button>
            )}
            {!disabled && (
              <StateButton
                id="submitAndNextButton"
                color="primary"
                onClick={handleSuccessButtonClick}
                next={() => {
                  setDisabled(true);
                }}
              >
                <IntlMessages id="forms.submit-and-next" />
              </StateButton>
            )}
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser } = authUser;
  return { currentUser };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Links);
